@font-face {
    font-family: 'Baloo', cursive;
    font-family: 'Play', sans-serif;
}

body {
    background: #F5F5F5;
    min-height: 75rem;
    font-family: 'Play', sans-serif;
    /* padding-top: 3.5rem; */
}

.flex-container {
    display: -ms-flexbox;
    display: flex;
}

.mynavbar {
    position: fixed;
    width: 100%;
}

.mynavbar-container {
    display: -ms-flexbox;
    display: flex;
    /* flex-direction: column; */
    padding-top: 0.5em;
    padding-bottom: 0.5em;
}

.mynavbar-brand {
    font-size: 1.6em !important;
    font-weight: bold;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-line-pack: center !important;
        align-content: center !important;
    -ms-flex-pack: center;
        justify-content: center;    
    padding: 10px !important;
}

.mynavbar-collapsible {
    /* display: none; */
    /* width:  */
}


.mynavbar-collapsible span {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-line-pack: center;
        align-content: center;
    -ms-flex-pack: center;
        justify-content: center;
    padding: 0px;
}

.menu-toggle {
    display: -ms-flexbox;
    display: flex;
    position: fixed;
    top: 25px;
    right: 25px;
    font-size: 1.6em !important;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-line-pack: end !important;
        align-content: flex-end !important;
    -ms-flex-pack: center;
        justify-content: center;
    cursor: pointer;
}

.menu-active {
    /* position: fixed; */
    -ms-flex-pack: center;
        justify-content: center;
    /* align-items: center; */
    display:-ms-flexbox;
    display:flex;
    /* width: 110%; */
    -ms-flex-direction: column;
        flex-direction: column;
    padding: 0px;
    -webkit-transition-duration: 0.1s;
         -o-transition-duration: 0.1s;
            transition-duration: 0.1s;
    -webkit-transition: 0.5s;
    -o-transition: 0.5s;
    transition: 0.5s;
    -webkit-animation: slide-menu 0.2s ease-in forwards;
            animation: slide-menu 0.2s ease-in forwards;
}

@-webkit-keyframes slide-menu {
    0% {
        -webkit-transform: translate(-15px,0);
                transform: translate(-15px,0);
        -webkit-transition-duration: 0.2s;
                transition-duration: 0.2s;
        opacity: 0;
        /* background: #ffffff; */
    }
    75% {
        -webkit-transform: translate(-10px,0);
                transform: translate(-10px,0);
        opacity: 0.5;
    }
    100% {
        -webkit-transition-duration: 0.2s;
                transition-duration: 0.2s;
        -webkit-transform: translate(-5px,0);
                transform: translate(-5px,0);
        -webkit-animation-delay: 1s;
                animation-delay: 1s;
        opacity: 1;
        font-weight: bold;
        /* background: #ffffff; */
    }
}

@keyframes slide-menu {
    0% {
        -webkit-transform: translate(-15px,0);
                transform: translate(-15px,0);
        -webkit-transition-duration: 0.2s;
             -o-transition-duration: 0.2s;
                transition-duration: 0.2s;
        opacity: 0;
        /* background: #ffffff; */
    }
    75% {
        -webkit-transform: translate(-10px,0);
                transform: translate(-10px,0);
        opacity: 0.5;
    }
    100% {
        -webkit-transition-duration: 0.2s;
             -o-transition-duration: 0.2s;
                transition-duration: 0.2s;
        -webkit-transform: translate(-5px,0);
                transform: translate(-5px,0);
        -webkit-animation-delay: 1s;
                animation-delay: 1s;
        opacity: 1;
        font-weight: bold;
        /* background: #ffffff; */
    }
}


footer {
    background: #ffffff;
    height: 20vh;
}

.homecontainer {
    /* background-image: linear-gradient(120deg, #a1c4fd 0%, #c2e9fb 100%); */
    /* background-image: linear-gradient(120deg, #84fab0 0%, #8fd3f4 100%); */
    background-image: -webkit-linear-gradient(330deg, #fdfbfb 0%, #ebedee 100%);
    background-image: -o-linear-gradient(330deg, #fdfbfb 0%, #ebedee 100%);
    background-image: linear-gradient(120deg, #fdfbfb 0%, #ebedee 100%);
    height: 100vh;
}

.homecontainer .container {
    padding-top: 30vh;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-line-pack: center;
        align-content: center;
    text-align: center;
}

.homecontainer .container .homeName {
    font-family: 'Baloo', cursive;
    /* color: #ffffff; */
    margin: 0;
}

.homecontainer .container .btn {
    font-size: 0.9em;
    padding: 5px 20px;
    -ms-flex-item-align: center;
        align-self: center;
    background: none;
    color: #56CC9D;
    /* width: 60%; */
}

.homecontainer .container .btn:hover{
    background: #56CC9D !important;
    color: #ffffff !important;
}


/* MEDIA SCREEN */
@media screen and (min-width:575px) {

    .mynavbar-container {
        -ms-flex-direction: row;
            flex-direction: row;
    }

    .mynavbar-collapsible {
        display: -ms-flexbox !important;
        display: flex !important;
    }

    .menu-toggle {
        display: none;
    }

    .homecontainer .container .homeName {
        font-family: 'Baloo', cursive;
        /* color: #ffffff; */
        margin: 0;
        font-size: 4em;
    }

    .homecontainer .container .homeTitle {
        font-size: 2em;
    }

    .homecontainer .container .btn {
        font-size: 1.5em;
    }
    
}


